<template>
  <div>
    <flow-mapping-form
      v-if="currentItems"
      :items="streams"
      :currentItems="currentItems"
      :onSave="onSave"
      title="Associate Streams"
      :onCancel="onCancel"
    ></flow-mapping-form>
    <v-overlay :value="isLoadingData">
      <v-progress-circular indeterminate size="32" color="primary"></v-progress-circular>
    </v-overlay>
  </div>
</template>
<script>
import FlowMappingForm from './flowMappingForm.vue';
import AssociateObjectStreams from './associateObjectStreams.vue';
import { rfStreamService } from '../../../services/rfStreamsService';
import { rfFlowNetworkService } from '../../../services/rfFlowNetworkService';
export default {
  name: 'associate-streams-form',
  props: ['flowNetworkId', 'onCancel', 'refreshGridCb'],
  components: { AssociateObjectStreams, FlowMappingForm },
  data() {
    return {
      isLoadingData: false,
      streams: [],
      currentItems: null,
    };
  },
  mounted() {
    this.fetchFlowNetworkById();
    this.fetchAllStreams();
  },

  methods: {
    fetchAllStreams() {
      return rfStreamService.fetch().then((data) => {
        this.streams = data;
      })
    },
    onSave(data) {
      return rfFlowNetworkService.patch(this.flowNetworkId, { streams: data }).then(data => {
        if (typeof this.refreshGridCb === 'function') this.refreshGridCb();
        return data;
      });
    },
    fetchFlowNetworkById() {
      this.isLoadingData = true;
      return rfFlowNetworkService
        .fetchById(this.flowNetworkId, { with_streams: true })
        .then(data => {
          this.currentItems = data.streams || [];
          return data;
        })
        .finally(() => {
          this.isLoadingData = false;
        });
    },
  },
};
</script>
