<template>
  <div>
    <generic-grid
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :service="service"
      :params="params"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
      :storeMutationKey="storeMutationKey"
    >
      <template v-slot:routeLinkName="{ item }">
        <text-button :text="item.name" :onClick="() => redirectTo(dialogNames.viewStreams, item)"></text-button>
      </template>
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <flow-network-form
          v-if="isOpen"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></flow-network-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <flow-network-form
          :item="item"
          v-if="isOpen"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></flow-network-form>
      </template>
      <template v-slot:gridItemActions="{ item, refreshGrid }">
        <icon-button
          title="Add Streams"
          icon="mdi-plus"
          color="nubeYellow"
          :onClick="() => toggleDialog(dialogNames.associateStreams, item, refreshGrid)"
        ></icon-button>
      </template>
    </generic-grid>

    <v-dialog
      persistent
      max-width="500px"
      v-if="selectedItem && selectedItem.uuid"
      v-model="dialog[dialogNames.associateStreams]"
    >
      <associate-streams
        v-if="dialog[dialogNames.associateStreams]"
        :refreshGridCb="refreshGridCb"
        :flowNetworkId="selectedItem && selectedItem.uuid"
        :onCancel="() => closeDialog(dialogNames.associateStreams)"
      ></associate-streams>
    </v-dialog>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import FlowNetworkForm from './flowNetworkForm.vue';
import IconButton from '../../Common/IconButton.vue';
import TextButton from '../../Common/TextButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import { rfFlowNetworkService } from '../../../services/rfFlowNetworkService';
import AssociateStreams from '../MapStreamFlowNetwork/associateStreams.vue';
const dialogNames = {
  associateStreams: 'associateStreams',
  viewStreams: 'viewStreams',
};

const redirectMap = {
  [dialogNames.viewStreams]: flowNetworkId => ROUTES.rfFlowNetworkStreams.replace(':flowNetworkId', flowNetworkId),
};

export default {
  components: { TextButton, GenericGrid, FlowNetworkForm, IconButton, AssociateStreams },
  name: 'flowNetworkGrid',
  data() {
    return {
      params: {},
      gridName: gridNames.RF_FLOW_NETWORK,
      service: rfFlowNetworkService,
      refreshGridCb: f => f,
      toolbarTitle: 'Flow Networks',
      storeMutationKey: 'changeRfFlowNetworks',
      selectedItem: {},
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.associateStreams]: false,
      },
      selectedItem: {},
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Grid Actions',
          align: 'center',
          value: 'gridItemActions',
          sortable: false,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  methods: {
    redirectTo(viewName, item) {
      const streamId = item.uuid;
      if (!streamId) {
        return;
      }
      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(streamId) });
      }
    },
    toggleDialog(dialogName, item, refreshGrid) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];
      this.refreshGridCb = refreshGrid;
      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
  },
};
</script>
